import React, { useEffect } from 'react';
import { Link } from "react-router-dom";
import list from './js/listHome'

const Home = () => {

    return (
        <section className="container">
            <h1 className="mainTitle text-center overflow-hidden display-1 text-dark">Catalogue</h1>

            <div className="row row-cols-1 row-cols-md-3 d-flex justify-content-center overflow-visible mb-2">
                {list.map((props, index) => {
                    return (
                        <Link to={"/" + props.link} className={`${props.link} m-2 col-10 position-relative rounded background d-flex align-items-center justify-content-center scaleOnHover`} id={props.id} key={index}
                            style={{ height: '35vh', maxHeight: '15rem' }}>
                            <h3 className="position-absolute h4 text-white text-center mx-2" style={{ zIndex: '10', fontWeight: '400', textTransform: 'uppercase' }}>{props.name}</h3>
                            <div className='position-absolute bg-dark container top-0 start-0 bg-opacity-50' style={{ height: '100%' }}>
                            </div>
                        </Link>
                    )
                })}
            </div>
        </section>
    )
}

export default Home;